/* App.css */

.page {
  position: relative;
  width: 1200px;
  height: 100vh;
}

.app {
  position: absolute;
  padding: 20px;
  border-radius: 8px;
  background-color: #fff;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: left 0.2s ease, top 0.2s ease; /* Add transition effect for smooth movement */
  cursor: pointer;
}

.app.following {
  transition: none; /* Remove transition effect while following mouse */
}

.app h1 {
  font-size: 1.5em;
  margin-bottom: 1em;
}

.app p {
  font-size: 1em;
  margin-bottom: 2em;
}

.app input[type="text"],
.app textarea {
  width: 100%;
  padding: 10px;
  margin-bottom: 1em;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.app button {
  display: block;
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.app button:hover {
  background-color: #0056b3;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #8a8a8a;
  color: #fff;
}

.navbar ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.navbar ul li {
  display: inline;
  margin-right: 20px;
}

.search-container {
  margin-top: 20px;
  width: 100%;
}

.search-container input[type="text"] {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.search-container button {
  padding: 8px 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.section {
  margin-top: 20px;
}

.section h2 {
  margin-bottom: 10px;
}

.category-container {
  display: flex;
  align-items: center;
}

.category-container h2 {
  margin-right: 10px;
}

.category-container select {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.category-container button {
  padding: 8px 16px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.category {
  margin: 30px;
}
.create-btn {
  padding: 8px 16px;
  background-color: #7aa3cf;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  margin-left: auto;
  width: 10px; /* Move the button to the right edge */
}

